      originalSource: "({\n  title = \"\uD0C0\uC774\uD2C0\",\n  description = \"\uADFC\uB85C\uC870\uAC74\uC758 \uAE30\uC900\uC740 \uC778\uAC04\uC758 \uC874\uC5C4\uC131\uC744 \uBCF4\uC7A5\uD558\uB3C4\uB85D \uBC95\uB960\uB85C \uC815\uD55C\uB2E4.\\n\uC774 \uD5CC\uBC95\uC5D0 \uC758\uD55C \uCD5C\uCD08\uC758 \uB300\uD1B5\uB839\uC758 \uC784\uAE30\uB294 \uC774 \uD5CC\uBC95\uC2DC\uD589\uC77C\uB85C\uBD80\uD130 \uAC1C\uC2DC\uD55C\uB2E4.\\n\uBC95\uB960\uC548\uC5D0 \uC774\uC758\uAC00 \uC788\uC744 \uB54C\uC5D0\uB294 \uB300\uD1B5\uB839\uC740 \uC81C1\uD56D\uC758 \uAE30\uAC04\uB0B4\uC5D0 \uC774\uC758\uC11C\uB97C \uBD99\uC5EC \uAD6D\uD68C\uB85C \uD658\uBD80\uD558\uACE0, \uADF8 \uC7AC\uC758\uB97C \uC694\uAD6C\uD560 \uC218 \uC788\uB2E4.\\n\uAD6D\uD68C\uC758 \uD3D0\uD68C\uC911\uC5D0\uB3C4 \uB610\uD55C \uAC19\uB2E4.\\n\" + \"\uAD6D\uAC00\uB294 \uC804\uD1B5\uBB38\uD654\uC758 \uACC4\uC2B9\xB7\uBC1C\uC804\uACFC \uBBFC\uC871\uBB38\uD654\uC758 \uCC3D\uB2EC\uC5D0 \uB178\uB825\uD558\uC5EC\uC57C \uD55C\uB2E4.\\n\uB204\uAD6C\uB4E0\uC9C0 \uCCB4\uD3EC \uB610\uB294 \uAD6C\uC18D\uC758 \uC774\uC720\uC640 \uBCC0\uD638\uC778\uC758 \uC870\uB825\uC744 \uBC1B\uC744 \uAD8C\uB9AC\uAC00 \uC788\uC74C\uC744 \uACE0\uC9C0\uBC1B\uC9C0 \uC544\uB2C8\uD558\uACE0\uB294 \uCCB4\uD3EC \uB610\uB294 \uAD6C\uC18D\uC744 \uB2F9\uD558\uC9C0 \uC544\uB2C8\uD55C\uB2E4.\\n\uCCB4\uD3EC \uB610\uB294 \uAD6C\uC18D\uC744 \uB2F9\uD55C \uC790\uC758 \uAC00\uC871\uB4F1 \uBC95\uB960\uC774 \uC815\uD558\uB294 \uC790\uC5D0\uAC8C\uB294 \uADF8 \uC774\uC720\uC640 \uC77C\uC2DC\xB7\uC7A5\uC18C\uAC00 \uC9C0\uCCB4\uC5C6\uC774 \uD1B5\uC9C0\uB418\uC5B4\uC57C \uD55C\uB2E4.\"\n}) => <DialogStory title={title} description={description} />",
